import IntlTelInput from 'intl-tel-input/reactWithUtils';

import "intl-tel-input/styles";
import { useState} from "react";
import {cn} from "@/utils";

const errorMap = [
  "Неправильный номер",
  "Неверный код страны",
  "Слишком короткий",
  "Слишком долгий",
  "Неправильный номер",
];

const PhoneInput = ({className, value, setValue,inputRef}) => {
  const [errorCode, setErrorCode] = useState(null);
  const [number, setNumber] = useState('');
  const [isValid, setIsValid] = useState(false);

  const onChaneNumberHandler = (number) => {
    setNumber(number);
    if (number && isValid) {
      setValue(number);
    } else {
      setValue('');
    }

  }
  const showNotice = () => {
    if (isValid) {
      return `Все отлично!`;
    } else {
      return errorMap[errorCode || 0] || "Неправильный номер";
    }
  }

  return (
    <div className={'relative'}>
      <IntlTelInput
        initialValue={value}
        ref={inputRef}
        onChangeNumber={onChaneNumberHandler}
        onChangeValidity={setIsValid}
        onChangeErrorCode={setErrorCode}
        inputProps={
          {
            required: !isValid,
            className: cn(
              "rounded-lg border border-neutral-300 focus:border-secondary-500 focus:ring-secondary-500 focus:ring-1",
              className,
              {"focus:border-red-500 border-red-500 focus:ring-red-500": number && !isValid},
              {'focus:border-green-500 border-green-500 focus:ring-green-500': isValid}
            )
          }
        }

        initOptions={{
          onlyCountries: ['ru','by', 'kz'],
          initialCountry: "ru",
          containerClass: "w-full",
          separateDialCode: true,
        }}
      />
      {number &&
        (<small className={cn(
          'absolute right-4 text-xs top-1/2 transform -translate-y-1/2',
          isValid ? 'text-green-500' : 'text-red-500'
        )}>
          {showNotice()}
        </small>)
      }
    </div>
  );
};

export default PhoneInput;