import PhoneInput from "@/components/UI/PhoneInput";
import Button from "@/components/UI/Button";
import {useRef, useState} from "react";
import {cn, useGetYmCode} from "@/utils";
import WhatsAppButton from "@/components/UI/WhatsAppButton";
import Input from "@/components/UI/Input";
import ym from "react-yandex-metrika";

// {"ring-accent-300 ring-2": isShake && phone.length === 0}
const BASE_URL = process.env.REACT_APP_BASE_URL;


const getAllUtms = () => {
  const utms = {};
  const utmRegex = /utm_(\w+)=([^&]+)/g;
  const url = window.location.href;
  let match;
  while ((match = utmRegex.exec(url)) !== null) {
    utms[match[1]] = match[2];
  }
  return utms;
}

async function sendFormToServer(data) {
  const response = await fetch(`${BASE_URL}`, {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
    },
    body: JSON.stringify(data),
  });
  if (!response.ok) {
    throw new Error('Ошибка при отправке формы');
  }
  return await response.json();
}

const Form = () => {
  const phoneRef = useRef();
  const [phone, setPhone] = useState('');
  const [name, setName] = useState('');
  const [isSend, setIsSend] = useState(false);
  const [msg, setMsg] = useState(null);
  const ymCode = useGetYmCode();
  const clearForm = () => {
    setName('');
    setPhone('');
    phoneRef.current?.getInstance().setNumber("");
    setMsg(null)
  }
  const canSend = !!name && phone && !isSend;

  async function sendForm(e) {
    e.preventDefault();
    setIsSend(true);
    const utms = getAllUtms();
    try {
      const resp = await sendFormToServer({name, phone, ...utms});
      if (!resp?.success) {
        setMsg({msg: resp?.message || 'Ошибка при отправке формы', class: 'text-red-500 bg-red-200'})
      }

      clearForm();
      if (ymCode) ym('reachGoal', 'lead');
      setMsg({msg: 'Спасибо за заявку! Мы свяжемся с вами в ближайшее время', class: 'text-green-700 bg-green-200'})

    } catch (e) {
      console.error(e)

      setMsg({msg: 'Ошибка при отправке формы', class: 'text-red-700 bg-red-200'})
    }
    setIsSend(false);
  }

  return (
    <form onSubmit={sendForm} className={'px-4  py-5 rounded-md bg-white lg:w-1/3 w-full'}>
      {msg && <p className={cn(
        'text-center p-2 rounded-lg mb-4',
        msg?.class
      )}>{msg?.msg}</p>}
      <legend className={'mb-5 text-xl'}>
        <strong>Бесплатная консультация</strong>
        <p>
          Оставьте телефон для связи
        </p>
      </legend>
      <Input
        type='text'
        placeholder='Ваше имя'
        className={'px-5 py-4 w-full mb-4'}
        value={name}
        required={!!name}
        onChange={e => setName(e.target.value)}
      />
      <PhoneInput
        className={cn("px-5 py-4 w-full")}
        inputRef={phoneRef} value={phone} setValue={setPhone}
      />

      <Button disabled={!canSend} className={'w-full mt-4 py-4 disabled:cursor-not-allowed disabled:opacity-70'}>
        Получить консультацию
      </Button>
      <WhatsAppButton/>

    </form>

  )
    ;
};

export default Form;